import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        {
            path: '/',
            name: 'index',
            redirect: '/call',
            component: resolve => require(['@/components/index/index'], resolve),
            children: [
                // 呼叫页面路由
                {
                    path: '/call',
                    name: 'call',
                    component: resolve => require(['@/components/call/call'], resolve),
                },
                // 写原由路由
                {
                    path: '/writeReason',
                    name: 'writeReason',
                    component: resolve => require(['@/components/call/writeReason'], resolve),
                },
                // 查看原由路由
                {
                    path: '/lookReason',
                    name: 'lookReason',
                    component: resolve => require(['@/components/call/lookReason'], resolve),
                },
                // 管理页面路由
                {
                    path: '/manage',
                    component: resolve => require(['@/components/manage/manage'], resolve),
                },
                // 排行榜路由
                {
                    path: '/manage/ranking',
                    component: resolve => require(['@/components/manage/ranking/ranking'], resolve),
                },
                // 排行榜=>我的成就路由
                {
                    path: '/manage/ranking/achivevment',
                    component: resolve => require(['@/components/manage/ranking/achivevment'], resolve),
                },
                // 我的页面路由
                {
                    path: '/mine',
                    component: resolve => require(['@/components/mine/mine'], resolve),
                },
            ]
        },
        // 统计页面路由==>暂时无用了
       /* {
            path: '/statistics',
            redirect: '/statistics/serve',
            component: resolve => require(['@/components/statistics/statistics'], resolve),
            children: [
                // 统计页面=>服务数据路由
                {
                    path: '/statistics/serve',
                    component: resolve => require(['@/components/statistics/serve'], resolve),
                },
            ]
        },*/
        // 统计页面路由
        {
            path: '/statistics/serve',
            component: resolve => require(['@/components/statistics/serve'], resolve),
        },
        // 统计页面=>服务类型路由
        {
            path: '/statistics/serve/servetype',
            component: resolve => require(['@/components/statistics/servetype'], resolve),
        },
        // 统计页面=>服务监测=>投诉路由
        {
            path: '/statistics/serve/servecomplain',
            component: resolve => require(['@/components/statistics/servecomplain'], resolve),
        },
        // 统计页面=>服务排行榜路由
        {
            path: '/statistics/serveRanking',
            component: resolve => require(['@/components/statistics/serveRanking'], resolve),
        },
        // 统计页面=>服务排行=>服务时长路由
        {
            path: '/statistics/serveRanking/serveTime',
            name: 'serveTime',
            component: resolve => require(['@/components/statistics/serveTime'], resolve),
        },
        // 统计页面=>实施监测路由
        {
            path: '/statistics/monitor',
            component: resolve => require(['@/components/statistics/monitor'], resolve),
        },
        // 统计页面=>实施监测路由
        {
            path: '/statistics/useRatio',
            component: resolve => require(['@/components/statistics/useRatio'], resolve),
        },
        // 管理中心=>客户意见路由
        {
            path: '/manage/suggest',
            component: resolve => require(['@/components/manage/suggest/suggest'], resolve),
        },
        // 管理中心=>客户意见=>留言详情路由
        {
            path: '/manage/suggest/suggestDetails',
            name: 'suggestDetails',
            component: resolve => require(['@/components/manage/suggest/suggestDetails'], resolve),
        },
        // 管理中心=>排行榜=>好评榜路由
        {
            path: '/manage/ranking/goodReputation',
            component: resolve => require(['@/components/manage/ranking/goodReputation'], resolve),
        },
        // 管理中心=>排行榜=>运动榜路由
        {
            path: '/manage/ranking/exerciseReputation',
            component: resolve => require(['@/components/manage/ranking/exerciseReputation'], resolve),
        },
        // 管理中心=>排行榜=>差评榜路由
        {
            path: '/manage/ranking/badReputation',
            component: resolve => require(['@/components/manage/ranking/badReputation'], resolve),
        },
        // 管理中心=>排行榜=>投诉榜路由
        {
            path: '/manage/ranking/complaintReputation',
            component: resolve => require(['@/components/manage/ranking/complaintReputation'], resolve),
        },
        // 正常评论路由
        {
            path: '/normalComment',
            component: resolve => require(['@/components/call/normalComment'], resolve),
        },
        // 异常评论路由
        {
            path: '/abnormalComment',
            component: resolve => require(['@/components/call/abnormalComment'], resolve),
        },
        // 待我审批路由
        {
            path: '/examine',
            component: resolve => require(['@/components/manage/examine/examine'], resolve),
        },
        // 请假申请路由
        {
            path: '/examine/staff',
            name: 'staff',
            component: resolve => require(['@/components/manage/examine/staff'], resolve),
        },
        // 请假申请路由管理员
        {
            path: '/examine/bossstaff',
            name: 'bossstaff',
            component: resolve => require(['@/components/manage/examine/bossstaff'], resolve),
        },
        // 请假路由
        {
            path: '/leave',
            component: resolve => require(['@/components/manage/leave/leave'], resolve),
        },
        // 加班路由
        {
            path: '/manage/overtime',
            component: resolve => require(['@/components/manage/overtime/overtime'], resolve),
        },
        // 出差界面路由
        {
            path: '/evection',
            component: resolve => require(['@/components/manage/evection/evection'], resolve),
        },
        // 考勤打卡路由
        {
            path: '/check',
            redirect: '/check/punchCard',
            component: resolve => require(['@/components/manage/check/check'], resolve),
            children: [
                // 打卡路由
                {
                    path: '/check/punchCard',
                    name: 'punchCard',
                    component: resolve => require(['@/components/manage/check/punchCard'], resolve),
                },
                // 申请路由
                {
                    path: '/check/apply',
                    component: resolve => require(['@/components/manage/check/apply'], resolve),
                },
                // 经理考勤统计路由
                {
                    path: '/check/bossturn',
                    component: resolve => require(['@/components/manage/check/bossturn'], resolve),
                },
                // 考勤信息路由
                {
                    path: '/check/bossturn/turnmanage',
                    name: 'turnmanage',
                    component: resolve => require(['@/components/manage/check/turnmanage'], resolve),
                },
                // 关于考勤信息通用模板路由
                {
                    path: '/check/bossturn/public',
                    name: 'public',
                    component: resolve => require(['@/components/manage/check/public'], resolve),
                },
                // 考勤统计路由
                {
                    path: '/check/turn',
                    component: resolve => require(['@/components/manage/check/turn'], resolve),
                },
            ],
        },
        // 考勤月历路由
        {
            path: '/check/month',
            component: resolve => require(['@/components/manage/check/month'], resolve),
        },
        // 补卡申请路由
        {
            path: '/check/month/repairCard',
            name: 'repairCard',
            component: resolve => require(['@/components/manage/check/repairCard'], resolve),
        },
        // 导出考勤报表路由
        {
            path: '/check/detail',
            component: resolve => require(['@/components/manage/check/detail'], resolve),
        },
        // 外出界面路由
        {
            path: '/check/apply/out',
            component: resolve => require(['@/components/manage/check/out'], resolve),
        },
        // 换班界面路由
        {
            path: '/check/apply/shifhs',
            component: resolve => require(['@/components/manage/check/shifhs'], resolve),
        },
        // 申请路由
        {
            path: '/check/apply/record',
            component: resolve => require(['@/components/manage/check/record'], resolve),
        },
        // 个人中心=>我的服务路由
        {
            path: '/mine/mineServe',
            component: resolve => require(['@/components/mine/mineServe'], resolve),
        },
        // 个人中心=>我的打赏路由
        {
            path: '/mine/mineTip',
            component: resolve => require(['@/components/mine/mineTip'], resolve),
        },
        // 个人中心=>被投诉的路由
        {
            path: '/mine/mineComplaint',
            component: resolve => require(['@/components/mine/mineComplaint'], resolve),
        },
        // 个人中心扫码之后判断是否登录
        {
            path: '/mine/allowLogin',
            component: resolve => require(['@/components/mine/allowLogin'], resolve),
        },
        // 登录界面路由
        {
            path: '/login',
            component: resolve => require(['@/components/login/login'], resolve),
        },
        // 测试路由
        {
            path: '/testCode',
            component: resolve => require(['@/components/testCode'], resolve),
        },
    ]
})

export default router;

