import VueLazyload from 'vue-lazyload'

import "./assets/css/index.css";
import "./assets/less/sea.less";
import './assets/iconfont/iconfont.css';


import commonJs from '@/assets/js/common.js';
Vue.prototype.common = commonJs;


import axios from 'axios';
Vue.prototype.$ajax = axios;

import Qs from 'qs';
Vue.prototype.$qs = Qs;


Vue.use(VueLazyload, {
preLoad: 1.3,
error: './static/err.png',

attempt: 1
})


Vue.filter('newData', function (value) {
if(value == undefined) {
return ""
}
var unixTimestamp = new Date( value*1000 ) ;
var mon = (unixTimestamp.getMonth()+1)< 10 ? '0'+ (unixTimestamp.getMonth()+1) : (unixTimestamp.getMonth()+1)
var day = (unixTimestamp.getDate())< 10 ? '0'+unixTimestamp.getDate() : unixTimestamp.getDate()
var hours = (unixTimestamp.getHours())< 10 ? '0'+unixTimestamp.getHours() : unixTimestamp.getHours()
var minutes = (unixTimestamp.getMinutes())< 10 ? '0'+unixTimestamp.getMinutes() : unixTimestamp.getMinutes()


return unixTimestamp.getFullYear() + "-" + (mon) + "-" + day + '/' + hours + ':' + minutes
})


Vue.filter('newDatas', function (value) {
if(value == undefined) {
return ""
}
var unixTimestamp = new Date( value*1000 ) ;
var mon = (unixTimestamp.getMonth()+1)< 10 ? '0'+ (unixTimestamp.getMonth()+1) : (unixTimestamp.getMonth()+1)
var day = (unixTimestamp.getDate())< 10 ? '0'+unixTimestamp.getDate() : unixTimestamp.getDate()




return unixTimestamp.getFullYear() + "-" + (mon) + "-" + day
})


Vue.filter('conversion', function (value) {
if(value == undefined) {
return ""
}
return Math.ceil(value/60)
})

Vue.config.productionTip = false;


