export default {
    name: 'app',
    data(){
        return{
            websock: null,
            show: false,    //  弹窗显示和隐藏
            cont_id: null,   // 订单id
            name: null,     // 服务类型
            boxName: null,   //  房间号
            num: null,   //  呼叫列表长度
        }
    },
    watch:{
        '$route':'getPath'
    },
    created(){
        this.common.commonFun.connect();
    },
    methods:{
        // 正常取消按钮
        onCancel () {
            if(this.$route.path == '/call'){
                window.location.href = 'https://demo.isaveu.cn/resources/#/call';
                this.$router.go(0)
            }
        },
        // 正常确定按钮
        onConfirm () {
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.admit,
                type: 'post',
                data:{
                    id: this.cont_id,
                },
                success: (response,allResponse) =>{
                    console.log(response)
                }
            });
            if(this.$route.path == '/call'){
                window.location.href = 'https://demo.isaveu.cn/resources/#/call';
                this.$router.go(0)
            }else{
                this.$router.push('/call')
            }
        },
        // 异常取消按钮
        Cancels(){
            window.location.href = 'https://demo.isaveu.cn/resources/#' + this.$route.path
        },
        // 异常确定按钮
        Confirm(){
            this.$router.push({
                path: '/writeReason',
                query: {
                    id: this.cont_id,
                }
            })
        },
        getPath(){
            if( this.$route.query.cont_id != undefined ){
                if( this.cont_id != this.$route.query.cont_id ){
                    this.show = true;
                    this.cont_id = this.$route.query.cont_id;
                    this.boxName = this.$route.query.box;
                    this.name = this.$route.query.name;
                    this.num = this.$route.query.num;
                }
            }
        }
    }

}




